body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch:after {
  content: "";
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
  position: absolute;
}

.stopwatch__minutes {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite time;
  position: absolute;
  top: 20vmin;
}

.stopwatch__seconds {
  transform-origin: 50% 100%;
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite time;
  position: absolute;
  top: 2vmin;
}

@keyframes time {
  to {
    transform: rotate(360deg);
  }
}

